.half-line-news {
  position: relative;
  background-color: #FFFFFF;
  border-radius: 8px;
  display: flex;
  align-items: flex-start;
  padding: 24px 24px 20px 24px;
  text-decoration: none;
  height: 177px;
}
.half-line-news__link {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
}
@media (max-width: 768px) {
  .half-line-news {
    height: 169px;
  }
}
@media (max-width: 767px) {
  .half-line-news {
    flex-direction: column;
    height: auto;
    padding: 0;
    flex-grow: 1;
    overflow: hidden;
    height: 310px;
    padding-bottom: 14px;
    display: flex;
    flex-direction: column;
  }
}
.half-line-news__content {
  padding-left: 24px;
}
.half-line-news__img-wrp {
  position: relative;
  flex-shrink: 0;
}
@media (max-width: 767px) {
  .half-line-news__img-wrp {
    width: 100%;
    height: 140px;
  }
}
.half-line-news__img-wrp::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.3);
  transition: all 0.3s;
}
.half-line-news__img {
  display: block;
}
@media (max-width: 767px) {
  .half-line-news__img {
    width: 100%;
    height: 140px !important;
    -o-object-fit: cover;
       object-fit: cover;
  }
}
.half-line-news__link:hover .half-line-news__img-wrp::after {
  background-color: rgba(0, 0, 0, 0);
}
.half-line-news__title {
  text-decoration: none;
  color: #000000;
  font-size: 17px;
  line-height: 22px;
  font-weight: 500;
  margin: 0 0 5px 0;
  max-height: 88px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 4;
}
@media (max-width: 768px) {
  .half-line-news__title {
    font-size: 15px;
    line-height: 18px;
  }
}
@media (max-width: 767px) {
  .half-line-news__title {
    font-weight: 500;
    font-size: 17px;
    line-height: 22px;
    padding-left: 0;
    margin: 16px 0 10px 0;
  }
}
.half-line-news__info {
  margin-left: 179px;
  position: absolute;
  bottom: 20px;
}
@media (max-width: 767px) {
  .half-line-news__info {
    margin: 0 20px;
    position: static;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}
.half-line-news__hashteg {
  margin-bottom: 8px;
  position: relative;
  z-index: 1;
}/*# sourceMappingURL=half-line-news.css.map */
